.card {
    background: #fff;
    border-radius: 2px;
  
    height: 160px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    display: inline-block;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:black;
  }
  .card > .img-container > img {
    height: 100%;
  }
  

.navbar{
    width: 100%;
    background: rgb(13, 152, 165);
    padding: 30px;
    text-align: center;
    
}
.title{
    font-size: 50px;
    margin: 0 auto;
    padding-bottom: 0;
    font-family: 'Fontdiner Swanky', cursive;
    color: rgb(255, 29, 17);
    text-shadow:
    -1px -1px 0 #000,  
     1px -1px 0 #000,
     -1px 1px 0 #000,
      1px 1px 0 #000;
}
.subtitle{
    font-size: 30px;
    margin: 0;
    padding: 0 auto 20px auto;
    font-family: 'Quicksand', sans-serif;
    color: white;
}
span{
    font-size: 40px;
    
}
#score{
    margin-left: 50px;
}
.gameOver{
    text-align: center;
    font-size: 5vw;
    font-family: 'Fontdiner Swanky', cursive;
}
button{
    width: 40vw;
    font-size: 30px;
